@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,500;0,700;1,100&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;;
}

body.leftSide{
  display: grid;
  height: 100%;
  width: 100%;
  background: -webkit-linear-gradient(left,#4361ee,#4cc9f0);
  place-items: center;
  background-color: #f2f2f2;
  transition: 0.6s;
  margin: 0;
}

body.rightSide{
  display: grid;
  height: 100%;
  width: 100%;
  background: -webkit-linear-gradient(left,#f72585,#7209b7);
  place-items: center;
  background-color: #f2f2f2;
  transition: 0.6s;
  margin: 0;
}

.container{
    max-width: 390px;
    background: #fff;
    padding: 30px;
    border-radius: 5px;
    box-shadow: 0px 15px 20px rgba(0,0,0,0.15);
    overflow: hidden;
}

.container .heading{
    display: flex;
    width: 200%;
    height: 42px;
    overflow: hidden;
}

.container .heading .title{
  width: 50%;
  font-size: 30px;
  font-weight: 600;
  text-align: center;
  padding: 0 10px;
  transition: all 0.6s ease;
}

.container .formBox{
  width: 100%;
  overflow: hidden;
}

.formBox .changeForm{
  position: relative;
  display: flex;
  height: 50px;
  width: 100%;
  overflow: hidden;
  border-radius: 5px;
  margin: 30px 0 10px 0;
  justify-content: space-between;
  border: 1px solid lightgrey;
}

.changeForm .change{
  height: 100%;
  width: 100%;
  z-index: 1;
  color: #fff;
  font-size: 18px;
  font-weight: 500;
  text-align: center;
  line-height: 50px;
  cursor: pointer;
  transition: all 0.6s ease;
}

.change.login.leftSide{
  color: #fff;  
}
.change.login.rightSide{
  color: #000;;
}
.change.signUp.leftSide{
  color: #000;
}
.change.signUp.rightSide{
  color: #fff;
}

#tab.changeTab.leftSide{
  position: absolute;
  height: 100%;
  width: 50%;
  left: 0;
  background: -webkit-linear-gradient(right,#4361ee,#4cc9f0);
  transition: all 0.6s linear;
  border-radius: 5px;
}

#tab.changeTab.rightSide{
  position: absolute;
  height: 100%;
  width: 50%;
  left: 0;
  background: -webkit-linear-gradient(left,#f72585,#7209b7);
  margin-left: 50%;
  transition: all 0.6s linear;
  border-radius: 5px;
}


.formBox .inner{
  display: flex;
  width: 200%;
}

.formBox .inner form{
  width: 50%;
  transition: all 0.6s ease;
}

.inner form .field{
  height: 50px;
  width: 330px;
  margin-top: 20px;
}

.inner form .field input{
  height: 100%;
  width: 100%;
  outline: none;
  padding-left: 15px;
  font-size: 17px;
  border-radius: 5px;
  border: 1px solid #d3d3d3;
  border-bottom-width: 2px;
  transition: border-color 1s ease;
}

.inner form .field input:focus{
  border-color: #4cc9f0;
}
.inner form .forgot{
  margin-top: 15px;
  text-align: right;
}

.inner form .forgot a{
  color: #4361ee ;
  text-decoration: none;
}

.inner form .forgot a :hover{
  text-decoration: underline;
}

form .field input[type = "submit"]{
  color: white;
  font-size: 20px;
  font-weight: 500;
  padding-left: 0px;
  border: none;
  cursor: pointer;
  /* background: -webkit-linear-gradient(right,#4361ee,#4cc9f0); */
}
.login .field input[type = "submit"]{
  background: -webkit-linear-gradient(right,#4361ee,#4cc9f0);
}

.signUp .field input[type = "submit"]{
  background: -webkit-linear-gradient(left,#f72585,#7209b7);    
}

.signUp .declare {
  margin-top: 15px;
  text-align: center;
  color: #d3d3d3;
}

.signUp .declare a{
  text-decoration: none;
}

.formBox .inner .wordWithLine{

  border-bottom: 1px solid #e5e7e8;
  line-height: 0.1em;
  width: 100%;
  margin: auto;
  margin: 15px 0 0 0;
  text-align: center;
  
}

.formBox .inner .wordWithLine .text{
  color: #9ca3a8;
  font-size: 1rem;
  background: #fff;
  padding: 0 10px;
}

.formBox .inner .google{
  width: 100%;
  height: 50px;
  margin-top: 15px;
  box-sizing: border-box;
  text-align: center;
  border-radius: 5px;
  border: 1px solid #d3d3d3;
  box-shadow: 0 1px 2px 0 rgb(0 0 0 / 30%);
}

.formBox .inner .google:hover{
  cursor: pointer;
}

.formBox .inner .google img{
  width: 25px;
  height: 45px;
  padding: 10px 0;
  display: inline-block;
  margin-right: 15px;
}

.formBox .inner .google p{
  height: 40%;
  display: inline-block;
  font-weight: 400;
  transform: translate(0,-80%);
}

.formBox .inner .login .google{
  margin-top: 25px;
}

.formBox .inner .secondSwitch{
  margin-top: 20px;
  text-align: center;
  color: #000;
  text-decoration: none;
  font-size: 14px;
  font-weight: 500;
}

.formBox .inner .secondSwitch a{
  text-decoration: none;
  color: #4361ee;
}